import React from 'react';
import '../style/inProgress.css'
import { useDispatch, useSelector } from 'react-redux';

const Inprogress = () => {
    const dispatch = useDispatch()
    const tasks = useSelector((state) => state.addTodoReducers.tasks)
    const inprogressTasks = tasks.filter(task => task.inprogress)
    console.log(inprogressTasks);

    let counter = 0;

    return(
        <>
        <div className="inprogress-container">
            <div className="inprogress-title">
                <div className="inprogress-title-text">
                    In Progress
                </div>
            </div>
            <div className="inprogress-items-wrapper">
                <div className="inprogress-items-wrapper-2">
                    {inprogressTasks.map(task => {
                        return <div key={task.id} className="inprogress-item">
                                <div className="inprogress-item-no">
                                    {++counter}.
                                </div>
                                <div className="inprogress-item-title">
                                    {task.title}
                                </div>
                            </div>
                    })}
                </div>
            </div>
        </div>
        </>
    )
}
export default Inprogress;