import React,{useState} from 'react';
import '../style/dailyTask.css'
import { useDispatch, useSelector } from 'react-redux';
import { addTodo, toggleTodo, deleteTodo, startTask } from '../redux/actions';

const AddDailyTask = () => {
    const dispatch = useDispatch();
    const [ title, setTitle ] = useState("")
    const tasks = useSelector((state) => state.addTodoReducers.tasks)

    const [ showForm , setShowForm ] = useState(false);

    let counter = 0;

    // ONCHANGE
    const handleOnChange = (e) => {
        setTitle(e.target.value);
    }
    // SUBMIT
    const handleSubmit = (e) => {
        e.preventDefault();

        if( title.trim() !== ''){
            dispatch(addTodo(title))
        }
        setTitle('')
        setShowForm(!showForm)
    }
    // TOGGLE
    const onToggle = (id) => {
        dispatch(toggleTodo(id))
    }
    // DELETE
    const deleteTask = (id) => {
        dispatch(deleteTodo(id))
    }
    // INPROGRESS
    const inProgress = (id) => {
            dispatch(startTask(id))
    }
    const completedTasks = tasks.filter(task => task.completed)
    console.log(completedTasks)

    const showFormHandler = () => {
        setShowForm(!showForm)
    }

    return(
        <>
        <div className="dailyTask-container">
            <div className="dailyTask-header">
                <div className="dailyTask-title"><div className="title-text">Daily Task</div></div>
                <div className="dailyTask-addTodo-btn" onClick={showFormHandler}>+</div>
            </div>
            <div className="dailyTask-list-wrapper">
                <div className="dailyTask-list">
                    {tasks.map(task => {
                        return <div key={task.id}  className="dailyTask-list-item">
                                <div className="dailyTask-list-caption">
                                    <div className="dailyTask-list-caption-no">
                                        <div className="dailyTask-list-caption-no-text">
                                            {++counter}
                                        </div>
                                    </div>
                                    <div className={task.completed ? "dailyTask-list-caption-title line-through": "dailyTask-list-caption-title"}>
                                        {task.title}
                                    </div>
                                </div>
                                <div className="dailyTask-list-item-btns">
                                <button onClick={() => task.completed ?  '': inProgress(task.id)} className="btn start-btn"><i className="fas fa-stopwatch"></i></button>
                                <button onClick={() => onToggle(task.id)} className="btn done-btn"><i className="far fa-check-square"></i></button>
                                <button onClick={() => deleteTask(task.id)} className="btn delete-btn"><i className="far fa-trash-alt"></i></button>
                                </div>
                                </div>
                    })}
                </div>
            </div>
            <div className={showForm ? "form-wrapper": "form-wrapper-hide"}>
                <form onSubmit={handleSubmit}>
                    <input value={title} onChange={handleOnChange} type="text"></input>
                    <button type="submit" className="addtask">ADD</button>
                </form>
            </div>
        </div>
        </>
    )
}
export default AddDailyTask;
