import React,{ useState} from 'react';
import '../style/projects.css'
import { useDispatch, useSelector } from 'react-redux';
import { addProjects, deleteProject, toggleProjects } from '../redux/actions';

const AddProjects = () => {

    const dispatch = useDispatch();
    const projects = useSelector((state) => state.addProjectsReducers.projects);
    const [ title, setTitle ] = useState('')
    console.log(projects)
    const [ showForm, setShowForm ] = useState(false);
    let counter = 0;

    // ONCHANGE
    const handleOnChange = (e) => {
        setTitle(e.target.value);
    }
    //SUBMIT
    const handleSubmit = (e) => {
        e.preventDefault();

        if(title.trim() !== '' ){
            dispatch(addProjects(title))
        }
        setTitle('')
        setShowForm(!showForm)
    }
    //TOGGLE
    const onToggle = (id) => {
        dispatch(toggleProjects(id))
    }
    //DELETE
    const deleteThis = (id) => {
        dispatch(deleteProject(id))
    }

    const showFormHandler = () => {
        setShowForm(!showForm)
    }


    return(
        <>
        <div className="projects-container">
            <div className="projects-header">
                <div className="projects-header-title"><div className="projects-header-title-text">Projects</div></div>
                <div className="addproject-btn" onClick={showFormHandler}>+</div>
            </div>
        <div className="projects-items-wrapper">
            <div className="projects-items-wrapper-2">
                    {projects.map(project => {
                    return <div key={project.id} className="project-list-item-wrapper">
                        <div className="counter">{++counter}</div>
                                    <div className="project-list-item-title">{project.title}</div>
                                        <div className="project-list-item-btns">
                                        {/* <button onClick={() => inProgress(project.id)} className="btn start-btn">Start</button> */}
                                        <button onClick={() => onToggle(project.id)} className="project-item-btn done-btn"><i className="far fa-check-square"></i></button>
                                        <button onClick={() => deleteThis(project.id)} className="project-item-btn delete-btn"><i className="far fa-trash-alt"></i></button>
                                        </div></div>
                })}
            </div>
        </div>
            <div className={showForm ? 'project-form-wrapper': 'project-form-wrapper-hide'}>
                <form onSubmit={handleSubmit}>
                    <input value={title} onChange={handleOnChange} type="text" placeholder="addTasks here"></input>
                    <button type="submit" className="addtask">ADD</button>
                </form>
            </div>
        </div>
        </>
    )
}
export default AddProjects;