import actionTypes from "./actionTypes";

const initialTasks = {
    tasks: []
}

export const addTodoReducers = ( state = initialTasks, { type, payload }) => {

    switch(type){
        case actionTypes.ADDTODO:{
            const { id, title} = payload
            return (
                {
                tasks: [
                    ...state.tasks,
                    {title, completed: false, inprogress: false,id}
                ]
            }
            )
        }
        case actionTypes.TOGGLE:{
            const {id} = payload
            const tasks = state.tasks.map(el => el.id === id ? { ...el ,completed: !el.completed,inprogress: false} : el);
            return (
                {
                    tasks
                }
            )
        }
        case actionTypes.DELETE:{
            const {id} = payload
            const tasks = state.tasks.filter(el => el.id !== id)
            return (
                {
                    tasks
                }
            )
        }
        case actionTypes.START:{
            const {id} = payload
            const tasks = state.tasks.map(el => el.id === id ? { ...el, inprogress: !el.inprogress} : el);
            return(
                {
                    tasks
                }
            )
        }
        default:
            return state;
    }
}
const initialProjects = {
    projects: []
}
export const addProjectsReducers = (state = initialProjects, { type, payload} ) => {

    switch(type){
        case actionTypes.ADDPROJECTS:{
            const { title, id} = payload;
            return {
                projects: [...state.projects,
                            {title: title, completed: false, id}]
            }
        }
        case actionTypes.TOGGLE_PROJECT:{
            const { id } = payload;
            console.log(state.projects)
            const projects = state.projects.map(el => el.id === id ? {...el,completed : !el.completed} : el)
            return({
                projects
            })
        }
        case actionTypes.DELETE_PROJECT:{
            const { id } = payload;
            const projects = state.projects.filter(el => el.id !== id)
            return({
                projects
            })
        }
        default:
            return state;
    }
}